;EACA,aAAA;EACA,aAAA;EACA,0BAAA;EACA,YAAA;EACA,kBAAA;EACA,qBAAA;CACA;AACA,cAAA;AACA;EACA,uBAAA;EACA,yBAAA;CAEA;AACA;EACA,kBAAA;CACA","file":"rewardSetup.vue","sourcesContent":["<template lang=\"html\">\n  <div class=\"\">\n    <herderBar></herderBar>\n    <div class=\"Setup\">\n      <div class=\"manager\">\n        <div class=\"title\">服务经理奖励设置</div>\n        <div class=\"Small_Setup\">\n          <div class=\"\">\n            <div>供应奖励设置</div>\n           \n          </div>\n          <div class=\"Setup_val\">\n            <input class=\"input\" v-model=\"managerData.ubi_reward\"/>\n            <span >元</span>\n          </div>\n        </div>\n        <div class=\"Small_Setup\">\n          <div class=\"\">\n            <div>开发奖励设置</div>\n            <div class=\"col-FA644B\">开发维修厂奖励金额</div>\n          </div>\n          <div class=\"Setup_val\">\n            <input class=\"input\" v-model=\"managerData.devel_reward\"/>\n            <span >元</span>\n          </div>\n        </div>\n         <div class=\"Small_Setup\">\n          <div class=\"\">\n            <div>开发运营商奖励设置</div>\n          </div>\n          <div class=\"Setup_val\">\n            <input class=\"input\" v-model=\"managerData.team_reward\"/>\n            <span >元</span>\n          </div>\n        </div>\n        <div class=\"Small_Setup\">\n          <div class=\"\">\n            <div>管理奖励分成</div>\n\n          </div>\n          <div class=\"Setup_val\">\n            <input class=\"input\" v-model=\"managerData.maid\"/>\n            <span>%</span>\n          </div>\n        </div>\n      \n        <div class=\"Preservation\">\n          <button @click=\"Preservation(1)\">保存</button>\n        </div>\n      </div>\n     <div class=\"manager\">\n        <div class=\"title\">期初配给设置</div>\n        <div class=\"Small_Setup\">\n          <div class=\"\">\n            <div>物料</div>\n           \n          </div>\n          <div class=\"Setup_val\">\n            <input class=\"input\" v-model=\"Earlystage.ration\"/>\n            <span >台</span>\n          </div>\n        </div>\n        <div class=\"Preservation\">\n          <button @click=\"Preservation(2)\">保存</button>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\n  export default {\n    data(){\n      return{\n        token:window.sessionStorage.getItem('bbytoken'),\n        managerData:{},  //存放服务经理里面的数据\n        Earlystage:{}, //存放运营总监奖励里面的数据\n      }\n    },\n    methods:{\n      //初始化数据,奖励设置内容\n      init(){\n        this.$axios.post('admin/SmSet/setList',{token:this.token}).then(res=>{  //奖励设置内容接口\n          if(res.data.code==1){\n          let data=res.data.data\n          this.managerData=data[0];\n          this.managerData['token']=this.token;\n        \n          this.ChiefInspector=data[1]\n          this.ChiefInspector['token']=this.token;\n        }else{\n          this.managerData['token']=this.token;\n          this.ChiefInspector['token']=this.token;\n        }\n       }).catch(err=>console.log(err))\n       /* 期初配给设置*/\n       this.$axios.post(\"admin/SmSet/rationSm\",{token:this.token}).then(res=>{\n         if(res.data.code==1){\n             this.Earlystage=res.data.data\n         }\n       })\n      },\n     // 点击保存\n      Preservation(e){\n          \n          if(e==1)this.modify_increase(this.managerData);\n          if(e==2)this.SetToConfigure(this.Earlystage)\n      },\n     //奖励设置修改、增加\n      modify_increase(data){\n        this.$axios.post('admin/SmSet/set',data).then(res=>{\n             if(res.data.code==1){\n                layer.msg(res.data.msg,{time:1000})\n             }else{\n                layer.msg(res.data.msg,{time:1000})\n              }\n        }).catch(err=>console.log(err))\n      },\n      /* 设置配给量 */\n      SetToConfigure(data){\n         this.$axios.post('admin/SmSet/joinLast',{token:this.token,ration:data.ration}).then(res=>{\n                 layer.msg(res.data.msg)\n                 if(res.data.code==1){\n                    this.init()\n                 }\n         }).catch(er=>console.log(err))\n      }\n      \n    },\n    mounted(){\n      this.init()\n    }\n  }\n</script>\n\n<style lang=\"css\" scoped=\"scoped\">\n  .Setup{\n    height: 1000px;\n    background-color: #f5f5f5;\n  }\n  /* 服务经理奖励设置 */\n  .manager{\n    background-color: #fff;\n    margin: 0 40px 10px 40px;\n  }\n  .manager>div{\n    margin-left: 40px;\n  }\n  .title{\n    padding: 20px 0;\n    border-bottom: 1px solid #E6E6EB\n  }\n  .Small_Setup:after{\n    display: block;\n    content: \"\";\n    clear: both;\n  }\n  .Small_Setup{\n    margin-top: 23px;\n  }\n  .Small_Setup>div:first-child{\n    width: 140px;\n  }\n  .Small_Setup>div{\n    float: left;\n    margin-right: 54px\n  }\n  .Setup_val{\n    position: relative;\n\n  }\n\n  .abs{\n    position:absolute;\n    right: -30px;\n    top: 10px\n  }\n  .input{\n    width: 150px;\n    height: 38px;\n    margin-right: 10px;\n    background:rgba(245,245,250,1);\n    border: 1px solid rgba(230,230,235,1);\n    text-align: center;\n  }\n  .taskNumber{\n    display: inline-block;\n    margin-right: 60px;\n  }\n  /* 保存 */\n  .Preservation>button{\n    width: 120px;\n    height: 40px;\n    background-color: #1EA0FF;\n    color: #fff;\n    line-height: 40px;\n    margin: 50px 0 28px 0\n  }\n  /* 运营总监奖励设置 */\n  .ChiefInspector{\n    background-color: #fff;\n    margin: 0 40px 10px 40px;\n\n  }\n  .ChiefInspector>div{\n    margin-left: 40px;\n  }\n</style>\n"],"sourceRoot":""}]);

